import React, { Component } from 'react';
import '../style/App.css';
import {connect} from "react-redux";
import Login from "./Login";
import Cart from "./Cart";
import History from "./History";
import Item from "./Item";
import * as firebase from "firebase";

export default class App extends Component {
    constructor(props){
        super(props);

        this.state = {
            user:{
                email:"",
                name:"",
                isLoggedIn:false
            },
            products:{},
            history:[],
            cart:[],
            showAside:true,
            showEdit: false
        };

        this.logIn = this.logIn.bind(this);
        this.logOut = this.logOut.bind(this);
        this.updateUserInput = this.updateUserInput.bind(this);
        this.addToCart = this.addToCart.bind(this);
        this.updateItem = this.updateItem.bind(this);
        this.viewShowEdit = this.viewShowEdit.bind(this);
    }

    logIn(){
        firebase.auth().signInAnonymously().catch(function(error) {
           console.log(error);
        });

        firebase.auth().onAuthStateChanged((user)=> {
            if (user) {
                // User is signed in.
                var isAnonymous = user.isAnonymous;
                var uid = user.uid;
                // ...
            } else {
                // User is signed out.
                // ...
            }
            // ...
        });
    }

    logOut(){

    }

    updateUserInput(e){
        let elm = e.target;

        if(elm.name === "name"){
            this.setState({
                user:{
                    email: this.state.user.email,
                    name:elm.value
                }
            });
        }
        else if(elm.name === "email"){
            this.setState({
                user:{
                    email:elm.value,
                    name:this.state.user.name
                }
            });
        }
    }

    addToCart(){

    }

    viewShowEdit(){
        this.setState({showEdit:!this.state.showEdit});
    }

    updateItem(){

        this.viewShowEdit();
    }


    render(){
        let testView = "user";
        const itemList = [
            <Item key="0" user={this.state.user}  addToCart={this.addToCart} showEdit={this.state.showEdit}
                  updateItem={this.updateItem}
            />,
            <Item key="1" user={this.state.user}  addToCart={this.addToCart} showEdit={this.state.showEdit}
                  updateItem={this.updateItem}
            />,
            <Item key="2" user={this.state.user}  addToCart={this.addToCart} showEdit={this.state.showEdit}
                  updateItem={this.updateItem}
            />,
            <Item key="3" user={this.state.user}  addToCart={this.addToCart} showEdit={this.state.showEdit}
                  updateItem={this.updateItem}
            />,
            <Item key="4" user={this.state.user}  addToCart={this.addToCart} showEdit={this.state.showEdit}
                  updateItem={this.updateItem}
            />,
            <Item key="5" user={this.state.user}  addToCart={this.addToCart} showEdit={this.state.showEdit}
                  updateItem={this.updateItem}
            />,
            <Item key="6" user={this.state.user}  addToCart={this.addToCart} showEdit={this.state.showEdit}
                  updateItem={this.updateItem}
            />,
            <Item key="7" user={this.state.user}  addToCart={this.addToCart} showEdit={this.state.showEdit}
                  updateItem={this.updateItem}
            />,
        ];
        return(
            <section >
                <div className="Header">
                    <h1>Uppg23Redux</h1>
                    <Login
                        logIn={this.logIn}
                        logOut={this.logOut}
                        isLoggedIn={this.state.isLoggedIn}
                        updateUser={this.updateUserInput}
                        name={this.state.user.name}
                        mail={this.state.user.email}
                    />
                </div>
                {!this.state.showAside &&
                    <div className="Main">
                        <div className="items">{itemList}</div>
                    </div>
                }
                {testView === "user" && this.state.showAside &&
                    <div className="Main">
                        <div className="items">{itemList}</div>
                        <Cart cart={this.state.cart}/>
                    </div>
                }
                {testView === "admin" && this.state.showAside &&
                    <div className="Main">
                        <div className="items">{itemList}</div>
                        <History history={this.state.history}/>
                    </div>
                }

            </section>
        );
    }
}
