import React, {Component} from "react";
import "../style/Cart.css";

export default class Cart extends Component{
     
    render(){

        let list = this.props.cart.map((x,index)=>{
            return <li key={index}>x</li>
        });

        return(
            <section className="Cart">
                <ul>
                    {list}
                </ul>
            </section>
        );
    }
}


