import React, {Component} from "react";
import "../style/ItemEdit.css";

export default class ItemInfo extends Component{
     
    render(){
        return(
            <section className="ItemEdit">
                <input type="text"/>
                <button onClick={this.props.updateItem}>DONE</button>
                <input type="text"/>
                <img src="http://www.drodd.com/images14/blue20.jpg" alt="item"/>
                <input type="text"/>
                <input type="text"/>
            </section>
        );
    }
}


