import initialState from "../index";
import TYPE from "../actions/types";

function rootReducer(action, state=initialState){
    switch(action.type){
      /*add product
        case TYPE.ADD_PRODUCT:
        case TYPE.REMOVE_PRODUCT:
        case TYPE.EDIT_PRODUCT:
            return {...state, products: products(state.products,action)};*/
        case TYPE.FETCH_PRODUCTS:
            return {...state, products:action.products};
        case TYPE.FETCH_HISTORY:
            return {...state, history:action.history};
        case TYPE.VIEW_CART:
            return {...state, showCart:action.showCart};
        case TYPE.VIEW_HISTORY:
            return {...state, showHistory:action.showHistory};
        case TYPE.LOG_IN:
            return {...state,
                user:{
                    name:action.name,
                    email:action.email,
                    password:action.password
                }
            };
        case TYPE.LOG_OUT:
            return {...state, user:false};
        default:
           return state;
    }
}

