import React, {Component} from "react";
import "../style/Item.css";
import ItemEdit from "./ItemEdit";

export default class Item extends Component{
     
    render(){
        let user = this.props.user;
        let testView = user.name === "Admin" && user.email === "Admin" ? "admin" : "user";


        return(
            <section className="Item">
                {testView === "user" &&
                <div>
                    <h4>title</h4>
                    <img src="http://www.drodd.com/images14/blue20.jpg" alt="item"/>
                    <p>description of item</p>
                    <button onClick={this.props.addToCart}>Add to cart</button>
                    <h5>10st</h5>
                </div>
                }
                {testView === "admin" &&
                <div>
                    <h4>title</h4>
                    <button onClick={this.props.viewShowEdit}>EDIT</button>
                    <img src="http://www.drodd.com/images14/blue20.jpg" alt="item"/>
                    <p>description of item</p>
                    <button onClick={this.props.addToCart}>Add to cart</button>
                    <h5>10st</h5>
                </div>
                }
                {testView === "admin" && this.props.showEdit &&
                    <ItemEdit updateItem={this.props.updateItem}/>
                }
            </section>
        );
    }
}


